@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500,600,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
html, body {
  color: #fff;
  font-family: 'Open Sans', sans-serif;
  letter-spacing: 1.9px;
  margin: 0px;
  padding: 0px;
  height: 100%;
}

body.modal-open {
  overflow: hidden;
}

#modal {
  background:#2a2a2f;
  z-index: 1;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 80%;
  height: 80%;
  box-shadow: 0 5px 10px 2px rgba(10, 31, 49, 1);
  overflow: auto;
  -ms-scroll-chaining: none;
      overscroll-behavior: contain /* Prevent SCROLL-CHAINING to parent elements. */
}

#modal button {
  z-index: 9999;
}

#root {
  background-color: rgba(10, 31, 49, 0.83);
  height: 100%;
}

@media all and (max-width: 1050px) {
  .welcomeHeader h1 {
    font-size: 3em;
  }

  #blogList {
    -webkit-animation: slide-sm 0.5s forwards;
            animation: slide-sm 0.5s forwards;
  }

  .welcomeHeader h2 {
    font-size: 2.4em;
  }

  #modal {
    width: 100%;
    height: 100%;
  }
}

